#search {
  font-size: 18px;
  border-radius: 5px;
  padding: 15px 20px;
  border:none;
  -webkit-box-shadow: 0px 1px 4px #aaaaaa;
          box-shadow: 0px 1px 4px #aaaaaa;
  width: 100%;
  color: #374146;
  -webkit-transition: 0.1s all ease-in-out;
  -o-transition: 0.1s all ease-in-out;
  transition: 0.1s all ease-in-out;
}

#search:focus {
  outline: none;
  -webkit-box-shadow: 0px 0px 4px 1px #bd63cf;
          box-shadow: 0px 0px 4px 1px #bd63cf;
}



