/* GENERAL */

.event {
  background-color: #fff;
  padding: 15px 20px;
  margin: 15px 0px 0px 0px;
  border-radius: 5px;
  -webkit-box-shadow: 0px 1px 4px #aaaaaa;
          box-shadow: 0px 1px 4px #aaaaaa;
  min-height: 70px;
  overflow: hidden;
  -webkit-transition: opacity 0.7s ease-in-out;
  -o-transition: opacity 0.7s ease-in-out;
  transition: opacity 0.7s ease-in-out;
}

.hidden {
  opacity: 0;
  height: 0;
  min-height: 0;
  -webkit-transition: opacity 0.35s ease-out;
  -o-transition: opacity 0.35s ease-out;
  transition: opacity 0.35s ease-out;
  line-height: 0;
  margin: 0;
  padding: 0;
}

.event:hover {
  -webkit-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.event .toprow {
  width: 100%;
  overflow: auto;
  height: 100%;
  margin: auto;
}

.event .title {
  float: left;
  max-width: 70%;
  text-align: left;
  font-size: 23px;
}

.event .time {
  font-size: 16px;
  max-width: 30%;
  float: right;
  text-align: right;
}

.event .bottomrow {
  font-size: 17px;
  padding: 0;
  width: 100%;
  -webkit-transition: 1s all ease-in-out;
  -o-transition: 1s all ease-in-out;
  transition: 1s all ease-in-out;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  -webkit-transition: max-height 0.25s ease-in-out;
  -o-transition: max-height 0.25s ease-in-out;
  transition: max-height 0.25s ease-in-out;
}

.event .description {
  max-width: 75%;
  text-align: left;
  position: relative;
  display: inline;
  float: left;
}

.event .location {
  float: right;
  max-width: 25%;
  text-align: right;
  font-weight: 600;
}

.content {
  width: calc(100% - 60px);
  float: left;
}

.buttons {
  float: right;
  max-width: 30px;
  min-height: 100%;
}

button:hover svg {
  fill: #545E63;
  -webkit-transition: 0.1s all ease-in-out;
  -o-transition: 0.1s all ease-in-out;
  transition: 0.1s all ease-in-out;
}

.buttons button {
  float: right;
  background: none;
  border: none;
  color: #ddd;
  cursor: pointer;
  -webkit-transition: 0.1s all ease-in-out;
  -o-transition: 0.1s all ease-in-out;
  transition: 0.1s all ease-in-out;
}

button:focus, button:active {
   outline: none !important;
   -webkit-box-shadow: none;
           box-shadow: none;
}

.event:hover .bottomrow {
  opacity: 1;
  max-height: 500px;
  padding: 10px 0;
  -webkit-transition: all 0.4s ease-in-out, padding 1ms;
  -o-transition: all 0.4s ease-in-out, padding 1ms;
  transition: all 0.4s ease-in-out, padding 1ms;
}

.blue:hover {
  -webkit-box-shadow: 0px 0px 4px 1px #16affb;
          box-shadow: 0px 0px 4px 1px #16affb;
}

.yellow:hover {
  -webkit-box-shadow: 0px 0px 4px 1px #fdf15f;
          box-shadow: 0px 0px 4px 1px #fdf15f;
}

.teal:hover {
  -webkit-box-shadow: 0px 0px 4px 1px #15cfb0;
          box-shadow: 0px 0px 4px 1px #15cfb0;
}

.red:hover {
  -webkit-box-shadow: 0px 0px 4px 1px #e03e59;
          box-shadow: 0px 0px 4px 1px #e03e59;
}

.green:hover {
  -webkit-box-shadow: 0px 0px 4px 1px #83c400;
          box-shadow: 0px 0px 4px 1px #83c400;
}

.purple:hover {
  -webkit-box-shadow: 0px 0px 4px 1px #bd63cf;
          box-shadow: 0px 0px 4px 1px #bd63cf;
}

/* NORMAL EVENT */


/* PERSONAL EVENT */
.personal .event {
  margin: 15px auto;
  width: 90%;
}

.personal .event:hover .bottomrow {
  padding: 0px;
  opacity: 0;
  max-height: 0;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

/* Small-medium width tablets: iPad mini */

@media only screen and (max-width: 1280px) {
  .event .title {
    font-size: 20px;
    max-width: 60%;
  }

  .event .time {
    max-width: 40%;
  }
}



